body {
    background-color: rgb(233, 227, 227);
}

* {
    margin: 0;
    padding: 0;
}

div.main-wrapper {
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    max-width: 600px;
    max-height: 80vh;
    background-color: white;
    box-shadow: 2px 10px 20px grey;
    height: 60vh;

    div.form-wrapper {
        form {
            display: flex;
            flex-direction: column;
            margin: auto;
            margin-top: 30px;

            div.input-wrapper {
                padding: 10px;
                width: 400px;
                margin: auto;

                .input-item {
                    height: 40px;
                    width: 100%;
                    border-radius: 5px;
                    border: 2px solid grey;
                }

                .input-error {
                    color: red;
                    font-style: italic;
                    font-size: smaller;
                }

                .submit {
                    width: 100%;
                    color: white;
                    background-color: green;
                    height: 40px;
                    border: 1px solid white;
                    border-radius: 0;
                }
            }
        }
    }

    .project-description-wrapper {
        width: 100%;
        margin: auto;
        display: flex;
        flex-direction: column;
        width: 80%;
        text-align: justify;

        p {
            margin-top: 20px;
            font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
            font-size: 1.3rem;
            padding-bottom: 20px;
        }

    }

    .button:active {
        border: 0;
        border-width: 0;

    }

    .button {
        margin-top: 20px;
        width: 100%;
        color: white;
        background-color: green;
        height: 40px;
        border: 1px solid white;
        border-radius: 0;
        width: 200px;
        margin: auto;
    }

    div.akanName-wrapper {
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-top: 40px;
        padding: 10px;

        p {
            margin-top: 20px;
            font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
            font-size: 1.3rem;
            padding-bottom: 20px;
        }
    }

}
